import { createStore, applyMiddleware, compose } from "redux";
import { routerMiddleware } from "react-router-redux";
import { persistStore, persistReducer } from "redux-persist";
import storage from "redux-persist/lib/storage"; // defaults to localStorage
/* import SocketIOClass from "./services/socketio" */
import createSagaMiddleware from "redux-saga";
import thunk from "redux-thunk";
import promise from "redux-promise-middleware";
import history from "@utterzone/connector";
import rootSaga from "./rootSaga.js";
import rootReducer from "./rootReducer.js";

/* export const SocketIO = new SocketIOClass() */

// redux-persist
var persistConfig = {
  key: "root",
  storage: storage
};

var persistedReducer = persistReducer(persistConfig, rootReducer);

// redux-dev tools requirement
var composeEnhancers = compose;

if (process.env.NODE_ENV !== "production" || process.env.NODE_ENV !== "prod") {
  composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
}

var sagaMiddleware = createSagaMiddleware();

var store = createStore(persistedReducer, composeEnhancers(applyMiddleware(sagaMiddleware, thunk, routerMiddleware(history), promise())));

var persistor = persistStore(store);

sagaMiddleware.run(rootSaga);

export { store, persistor };
